import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import { Alert } from "reactstrap";

import { Loading } from "@ui/components/util/Loading";
import { IndexingReview } from "./IndexingReview";
import { JobManager } from "./JobManager";

export const App = () => {
  return (
    <div className="bg-white overflow-hidden vh-100 vw-100">
      <HashRouter>
        <Switch>
          <Route path="/:token/jobs/:jobId">
            <JobManager>
              {({ job, jobError, token }) => {
                if (jobError) {
                  return <Alert color="danger">{jobError.message}</Alert>;
                } else if (job === null) {
                  return <Loading text="Loading..." />;
                }
                return <IndexingReview job={job} token={token} />;
              }}
            </JobManager>
          </Route>
          <Route path="*">
            <p>Page not found.</p>
          </Route>
        </Switch>
      </HashRouter>
    </div>
  );
};
App.displayName = "App";
